.brk-sc-row-four-pagination-bullet {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid transparent;
  position: relative;
  margin: 0 7px;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.brk-sc-row-four-pagination-bullet:before {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all .3s ease-in-out;
  background-color: var(--white);
}

.brk-sc-row-four-pagination-bullet-active {
  border: 1px solid #dbdbdb;
}

.brk-sc-row-four-pagination-bullet-active:before {
  opacity: 0;
}

.brk-sc-angle-post {
  padding: 43px 15px 43px;
  outline: 0;
}

.brk-sc-angle-post__container {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  position: relative;
  background: #d1d1d1 no-repeat center center;
  background-size: contain;
}

.brk-sc-angle-post__container-before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all .3s	ease-in-out;
}

.brk-sc-angle-post__container:hover .brk-sc-angle-post__container-before {
  opacity: 1;
}

.brk-sc-angle-post__container:hover .brk-sc-angle-post__title {
  top: 50%;
  transform: translate(-50%, -50%) scale(1);
}

.brk-sc-angle-post__container:hover .brk-sc-angle-post__price {
  bottom: 0;
  transform: translateX(-50%) scale(0);
}

.brk-sc-angle-post__container:hover .brk-sc-angle-post__buy {
  bottom: -23px;
  transform: translateX(-50%) scale(1);
}

.brk-sc-angle-post__title {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  max-width: 180px;
  text-align: center;
  transform: translateX(-50%) scale(0);
  font-size: 1.3125rem;
  line-height: 26px;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0 15px;
  transition: all .4s	ease-in-out;
  z-index: 3;
}

.brk-sc-angle-post__price {
  position: absolute;
  left: 50%;
  bottom: -23px;
  transform: translateX(-50%);
  min-width: 76px;
  text-align: center;
  font-size: 1.125rem;
  line-height: 46px;
  border-radius: 3px;
  font-weight: bold;
  padding: 0 14px;
  transition: all .4s ease-in-out;
  z-index: 3;
  box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
}

.brk-sc-angle-post__price span {
  display: inline-block;
  font-size: 0.5625rem;
  line-height: 42px;
  margin-left: -3px;
}

.brk-sc-angle-post__buy {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) scale(0);
  width: 100%;
  max-width: 176px;
  font-size: 0.8125rem;
  text-transform: uppercase;
  line-height: 46px;
  border-radius: 3px;
  font-weight: bold;
  padding: 0 15px;
  text-align: center;
  transition: all .4s ease-in-out;
  z-index: 3;
  box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
}

.brk-sc-angle-post__buy i {
  vertical-align: 0;
  font-weight: normal;
  font-size: 1rem;
  margin-right: 12px;
}
